import React, { Component } from 'react';
import Icon from '@mdi/react';
import { mdiChevronLeft, mdiChevronRight, mdiCodeTags, mdiBookOpenOutline, mdiDraw, mdiCodeJson } from '@mdi/js';
// https://materialdesignicons.com/

class Header extends Component {
  constructor(props){
    super(props)
    console.log(props.hdState)
  }

  render(){
    let hdIcoName = this.props.hdState === true ? mdiChevronLeft : mdiChevronRight;
    let hdActClass = this.props.hdState === true ? 'act' : '';
    return (
      <header className={hdActClass}>
        {/*
        <button type="button" className="gnb_btn" onClick={this.props.hdStateE}>
          <Icon path={hdIcoName} size={1} color="#fff"/>
        </button>
        */}
        <div className="hd_wrap">
          <div className="hd_cont">
            <h1>
              <a href="/">
                <Icon path={mdiCodeTags} size={2} color="#c22"/>
                <span className="h1_txt">Coding Convention</span>
              </a>
            </h1>
            <nav>
                <ul>
                  <li><a href="convention"><Icon path={mdiBookOpenOutline} size={1} color="#666" /><span className="li_txt">코딩컨벤션</span></a>
                    <ul>
                      <li><a href="namingRule">네이밍 규칙</a></li>
                      {/*
                      <li><a href="html">HTML</a></li>
                      <li><a href="htmlRule">CSS</a></li>
                      <li><a href="htmlRule">JS</a></li>
                      */}
                    </ul>
                  </li>
                  <li><a href="commonjs"><Icon path={mdiCodeJson} size={1} color="#666" /><span className="li_txt">common.js</span></a>
                    <ul>
                      <li><a href="reqform">필수입력</a></li>
                      <li><a href="etcjs">기타</a></li>
                      {/*
                      <li><a href="selectNav">Select Nav</a></li>
                      <li><a href="mailSelector">이메일 주소 선택</a></li>
                      <li><a href="address_selector">주소 선택</a></li>
                      <li><a href="ePopup">새창팝업</a></li>
                      */}
                    </ul>
                  </li>
                  <li><a href="designComponent"><Icon path={mdiDraw} size={1} color="#666" /><span className="li_txt">Design Commponent</span></a>
                    <ul>
                      <li><a href="button">Button</a></li>
                      <li><a href="input">Input text/textarea</a></li>
                      <li><a href="checkRadio">Input check/radio</a></li>
                    </ul>
                  </li>
                </ul>
            </nav>
          </div>
        </div>
      </header>
    )
  }
}

export default Header;
