import React, { Component } from 'react';
import Header from './component/header';

import Index from './component/page/index';

import NamingRule from './component/page/convention/namingRule';
import Html from './component/page/convention/html';
import Js from './component/page/convention/js';
import Css from './component/page/convention/css';

import Commonjs from './component/page/commonjs/commonjs';
import ReqForm from './component/page/commonjs/reqform';
import Etcjs from './component/page/commonjs/etcjs';


import Button from './component/page/designcomp/button';
import Input from './component/page/designcomp/input';
import CheckRadio from './component/page/designcomp/check_radio';


import Agree from './component/page/agree';

import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import './App.css';


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      hdOpen: false,
      subject:{title: 'poppdh', sub: 'CodingConvention'}
    }
  }
  hdMenuOpen = () => {
    if(this.state.hdOpen === false){
      this.setState({
        hdOpen: true
      });
    }else{
      this.setState({
        hdOpen: false
      });
    }
  }
  render(){
    return (
      <Router>
      <div className="App">
        <Header hdStateE={this.hdMenuOpen} hdState={this.state.hdOpen}></Header>
        <div id="content">
          <Switch>
            <Route path="/" exact component={Index}/>
            <Route path="/convention" exact component={Index}/>

            <Route path="/Html" component={Html}/>
            <Route path="/js" component={Js}/>
            <Route path="/css" component={Css}/>

            <Route path="/commonjs" component={Commonjs}/>
            <Route path="/reqform" component={ReqForm}/>
            <Route path="/etcjs" component={Etcjs}/>


            <Route path="/namingRule" component={NamingRule}/>

            <Route path="/button" component={Button}/>
            <Route path="/input" component={Input}/>
            <Route path="/checkRadio" component={CheckRadio}/>


            <Route path="/agree" component={Agree}/>
          </Switch>
        </div>
      </div>
      </Router>
    );
  }
}


export default App;
