import React, { Component } from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


class Index extends Component {
    constructor(props){
      super(props)
    }

    render(){
      const conv1_2_4 =
`.main { margin: 0 auto 30px; padding: 10px 0 }  // X
.main{margin: 0 auto 30px;padding: 10px 0}  // O`
      return (
        <article>
            <h2>코딩 컨벤션</h2>
            <h3>코딩 컨벤션의 필요성</h3>
            <p className="nm_txt nm_txt3 pb10">마크업 개발은 Front-end 페이지의 기본 골격을 형성하기 때문에 디자인, 브라우저, 스크립트, 성능, 접근성 등과 긴밀한 관계가 있다. 즉, 마크업 개발을 잘 해야 모든 브라우저에서 콘텐츠를 손실 없이, 빠르고 쉽게 사용자에게 전달할 수 있다. 코딩 컨번션은 이러한 조건을 만족시키기 위해 마크업 개발자가 지켜야할 표준을 제시한다.</p>
      			<p className="nm_txt nm_txt3 pb30">또한, 유지보수에 투자되는 비용을 최소화하기 위해 통일된 코드 작성법을 제시한다. 코드를 최초로 작성한 사람이 끝까지 유지보수할 확률은 매우 낮다. 따라서, 최초 개발자가 아닌 사람도 코드를 빠르고 정확하게 이해할 수 있도록 작성하는 것은 코드의 유지보수 비용을 절감하고 업무의 효율을 높이는 데 결정적인 역할을 한다.</p>
            <h3>컨번센 요소</h3>
            <h4>네이밍 규칙</h4>
            <p className="nm_txt nm_txt3 pb30">네이밍 규칙은 레이아웃, 객체, 이미지, 폴더, 파일의 이름을 작성하는 규칙이다. 이해하기 쉬운 이름으로 작성해야 코드를 쉽게 파악할 수 있다.</p>

            <h4>CSS 코드 작성 규칙</h4>
            <p className="nm_txt nm_txt3 pb10"><strong>들여쓰기 : </strong>CSS 코드는 특별한 경우를 제외하고는 들여쓰기를 하지 않는다. (@media, scss)</p>
            <p className="nm_txt nm_txt3 pb10"><strong>공백 : </strong>CSS 코드는 공백을 최소화한다.</p>
            <SyntaxHighlighter language="css" className="mb20" style={docco}>
                {conv1_2_4}
            </SyntaxHighlighter>
            <p className="nm_txt nm_txt3 pb10"><strong>빈 줄 : </strong>CSS 코드의 빈 줄은 코드 그룹의 영역을 표시하기 위하여 사용한다.</p>
            <p className="nm_txt nm_txt3 pb10"><strong>줄 바꿈 : </strong>CSS 코드의 가독성을 위한 줄 바꿈은 하지 않는다.</p>
            <p className="nm_txt nm_txt3 pb10"><strong>인코딩 : </strong>CSS의 인코딩은 HTML의 인코딩과 동일하게 선언하여 HTML 문서가 브라우저에서 바르게 해석될 수 있도록 한다.</p>
            <p className="nm_txt nm_txt3 pb10"><strong>선택자 : </strong>선택자 버그가 발생하지 않도록 규칙을 준수한다.</p>
            <p className="nm_txt nm_txt3 pb10"><strong>속성 선언 순서 : </strong>속성 선언 순서에 준수하며, CSS 코드 최적화를 위해 속성 값을 축약하고 사용하고 약식 속성을 허용 범위에 맞게 사용한다.</p>
            <ol className="mb20 num_lst">
              <li><strong>형태, 위치, 흐름</strong> display, overflow, float, position, z-index</li>
              <li><strong>크기</strong> width, height</li>
              <li><strong>간격</strong> padding, margin</li>
              <li><strong>선</strong> border</li>
              <li><strong>글자</strong> font</li>
              <li><strong>배경</strong> background</li>
            </ol>

            <h3>파일 관리</h3>
            <h4>마크업</h4>
            <p className="nm_txt nm_txt3 pb10">디자인 시안 파일명과 동일한 파일명을 사용한다.</p>
            <h4>css</h4>
            <p className="nm_txt nm_txt3">scss로 작성한 파일이 컴파일을 통해 css파일로 변환된다.</p>
            <p className="nm_txt nm_txt3"><strong>common : </strong>코드 기본 스타일 초기화 및 자주 사용하는 코드 설정</p>
            <p className="nm_txt nm_txt3"><strong>d_component : </strong>디자인 컴포넌트로 지정된 input, checkbox, radiobox, button 스타일</p>
            <p className="nm_txt nm_txt3"><strong>shop : </strong>쇼핑몰용 스타일</p>
            <p className="nm_txt nm_txt3 pb10"><strong>layout : </strong>디자인 페이지에 대한 전체적인 스타일</p>

        </article>
      )
    }
}

export default Index;
