import React, { Component } from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


class Etcjs extends Component {
    constructor(props){
      super(props)
    }

    render(){
        const OnlyNum =
  `<input type="text" name="" class="only_num"/>`
        const SelectNav =
`<select name="" class="js_link">
    <option value="https://www.inday.co.kr/">인데이</option>
    <option value="https://www.naver.com/">네이버</option>
</select>`
        const MailSel =
`$('엘리먼트').on('submit', '폼클래스', function(e){
    e.preventDefault();
    var $this = $(this);
    var reqReturn = reqFormCheck(e, $this);
    if(reqReturn == true){
        // 확장 코드입력
    }
});`
      return (
        <article>
            <h2>common.js</h2>
            <h3 className="nm_txt nm_txt3">숫자만 입력</h3>
            <p className="nm_txt nm_txt3">input에서 숫자만 입력이 필요할 경우 사용한다.(전화번호, 수량등)</p>
            <p className="nm_txt nm_txt3"><strong className="code">.only_num</strong> 클래스 사용</p>
            <SyntaxHighlighter language="html" className="mb20" style={docco}>
                {OnlyNum}
            </SyntaxHighlighter>
            <h3 className="nm_txt nm_txt3">Select 페이지 이동</h3>
            <p className="nm_txt nm_txt3"><strong className="code">.js_link</strong> select 선택시 value값으로 페이지 이동</p>
            <SyntaxHighlighter language="html" className="mb20" style={docco}>
                {SelectNav}
            </SyntaxHighlighter>
            {/*
            <h3 className="nm_txt nm_txt3">이메일 주소 선택</h3>
            <p className="nm_txt nm_txt3">이메일 아이디, url을 따로 입력시 사용</p>
            <SyntaxHighlighter language="js" className="mb20" style={docco}>
                {MailSel}
            </SyntaxHighlighter>
            <p className="nm_txt nm_txt3"><strong className="code">.req_inp</strong> 필수 입력이 필요한 input, select, textarea에 추가한다.</p>
            <p className="nm_txt nm_txt3"><strong className="code">data-alert</strong> alert로 출력될 항목명과 조사(을, 를등)까지 입력한다.</p>
            <p className="nm_txt nm_txt3">submit시 필요한 정보가 입력되지 않았을 경우 이벤트 취소와 alert, 해당 엘리먼트 focus된다.</p>
            <p className="nm_txt nm_txt3 pb30">필수입력 항목이 모두 입력된 경우 submit을 실행한다.</p>
            <h4 className="nm_txt nm_txt3">확장코드</h4>
            <SyntaxHighlighter language="js" className="mb20" style={docco}>
                {MailSel}
            </SyntaxHighlighter>
            <p className="nm_txt nm_txt3">필수 입력 확인 후 submit하지 않고 ajax등 따로 처리가 필요한 경우 사용한다.</p>
            <p className="nm_txt nm_txt3">필수 입력 항목의 입력, 선택 값이 있는 경우 true반환한다.</p>
            */}
        </article>
      )
    }
}

export default Etcjs;
