import React, { Component } from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


class Input extends Component {
    constructor(props){
      super(props)
      this.asdf();
    }
    asdf(){
      console.log('bb');
    }
    render(){

      const inpTxt = `<input type="text" name="" class="de_inp sm" placeholder="내용" />
<input type="text" name="" class="de_inp ss" placeholder="내용" />
<input type="text" name="" class="de_inp st" placeholder="내용" />
<input type="text" name="" class="de_inp st" placeholder="내용" disabled />`;

    const btnOutline = `<button class="de_btn sg outline">BUTTON</button>
<button class="de_btn sl outline">BUTTON</button>
<button class="de_btn sm outline">BUTTON</button>
<button class="de_btn ss outline">BUTTON</button>
<button class="de_btn st outline">BUTTON</button>
<button class="de_btn sg outline" disabled>BUTTON</button>`;

    const txtarea = `<textarea name="" cols="30" rows="10" class="de_inp sm" placeholder="내용"></textarea>
<textarea name="" cols="30" rows="10" class="de_inp ss" placeholder="내용"></textarea>
<textarea name="" cols="30" rows="10" class="de_inp st" placeholder="내용"></textarea>
<textarea name="" cols="30" rows="10" class="de_inp sm" placeholder="내용" disabled></textarea>`;

    const inpRadius = `<textarea name="" cols="30" rows="10" class="de_inp st r30" placeholder="내용"></textarea>
<input type="text" name="" class="de_inp ss r30" placeholder="내용" />
<input type="text" name="" class="de_inp sm r15" placeholder="내용" />
<input type="text" name="" class="de_inp sm r3" placeholder="내용" disabled />`;

      return (
        <article>
            <h2>Input(text), textarea</h2>
            <h3>클래스 <strong className="code">de_inp</strong> 로 사용</h3>
            <h4>focus 색상은 scss에서 공통 적용</h4>
            <div className="component_tb">
                <table>
                  <tbody>
                    <tr>
                        <th>class</th>
                        <th><strong className="code">sm</strong></th>
                        <th><strong className="code">ss</strong></th>
                        <th><strong className="code">st</strong></th>
                        <th>disabled</th>
                    </tr>
                    <tr>
                        <td><strong className="code">de_inp</strong></td>
                        <td><input type="text" name="" className="de_inp sm" placeholder="내용" /></td>
                        <td><input type="text" name="" className="de_inp ss" placeholder="내용" /></td>
                        <td><input type="text" name="" className="de_inp st" placeholder="내용" /></td>
                        <td><input type="text" name="" className="de_inp st" placeholder="내용" disabled /></td>
                    </tr>
                    <tr>
                      <td>input text code</td>
                      <td colSpan="4" className="code">
                        <SyntaxHighlighter language="html" style={docco}>
                            {inpTxt}
                        </SyntaxHighlighter>
                      </td>
                    </tr>
                    <tr>
                        <td><strong className="code">de_inp</strong></td>
                        <td><textarea name="" cols="30" rows="10" className="de_inp sm" placeholder="내용"></textarea></td>
                        <td><textarea name="" cols="30" rows="10" className="de_inp ss" placeholder="내용"></textarea></td>
                        <td><textarea name="" cols="30" rows="10" className="de_inp st" placeholder="내용"></textarea></td>
                        <td><textarea name="" cols="30" rows="10" className="de_inp sm" placeholder="내용" disabled></textarea></td>
                    </tr>
                    <tr>
                      <td>textarea code</td>
                      <td colSpan="4" className="code">
                        input text의 높이 *5
                        <SyntaxHighlighter language="html" style={docco}>
                            {txtarea}
                        </SyntaxHighlighter>
                      </td>
                    </tr>
                    <tr>
                        <td>border-radius</td>
                        <td><textarea name="" cols="30" rows="10" className="de_inp st r30" placeholder="내용"></textarea></td>
                        <td><input type="text" name="" className="de_inp ss r30" placeholder="내용" /></td>
                        <td><input type="text" name="" className="de_inp sm r15" placeholder="내용" /></td>
                        <td><input type="text" name="" className="de_inp sm r3" placeholder="내용" disabled /></td>
                    </tr>
                    <tr>
                      <td>radius code</td>
                      <td colSpan="4" className="code">
                        <SyntaxHighlighter language="html" style={docco}>
                            {inpRadius}
                        </SyntaxHighlighter>
                      </td>
                    </tr>
                  </tbody>
                </table>
            </div>


        </article>
      )
    }
}

export default Input;
