import React, { Component } from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


class Html extends Component {
    render(){
        const dtd = `<!DOCTYPE html>`;
        const htmlInit = `<!-- 상황에 따라 수정 사용 가능 -->
    <div id="wrap">
        <div id="header">
            <div class="gnb"></div>
            <div class="lnb"></div>
        </div>
        <div id="content"> 내용 </div>
        <div id="footer">하단정보(회사명, 주소, 전화번호 등)</div>
    </div>
    
    <div class="container">컨텐츠 사이즈를 잡아주는 엘리먼트</div>`;
      return (
        <article>
            <h2>HTML 코드 작성 규칙</h2>
            <p className="nm_txt nm_txt3 pb10">DTD(Document Type Definition)는 html5로 선언한다.</p>
            <SyntaxHighlighter language="html" className="mb20" style={docco}>
                {dtd}
            </SyntaxHighlighter>
            <p className="nm_txt nm_txt3 pb10">HTML의 기본 구조를 나타낸 코드이다.</p>
            <SyntaxHighlighter language="html" className="mb20" style={docco}>
                {htmlInit}
            </SyntaxHighlighter>

            <h3>id 및 class 네이밍 규칙</h3>
            <p className="nm_txt nm_txt3 pb10">id는 camel case 방식으로 작성한다.(camelCase)</p>
            <p className="nm_txt nm_txt3 pb10">class는 snake case 방식으로 작성한다. (snake_case)</p>

            <p className="nm_txt nm_txt3 pb10"><strong>들여쓰기 : </strong>HTML 코드를 작성할 때 코드의 가독성을 높이기 위하여 왼쪽 첫 번째 열부터 오른쪽으로 4칸 띄어 쓴다. 들여쓰기를 하면 전체 HTML 구조를 쉽게 파악할 수 있다.</p>
            <p className="nm_txt nm_txt3 pb10"><strong>빈줄 : </strong>HTML 코드의 빈 줄은 코드 그룹의 영역을 표시하기 위하여 사용한다.</p>
            <p className="nm_txt nm_txt3 pb30"><strong>주석 : </strong>HTML 코드의 주석은 코드 그룹을 구분하거나, 코드의 수정과 삭제를 표시하거나, 개발자가 참고해야 하는 사항을 기술한다.</p>
            <h3>HTML 엘리먼트 작성 규칙</h3>
            <p className="nm_txt nm_txt3 pb30">HTML 엘리먼트 작성 규칙은 반드시 선언해야 하는 애트리뷰트와 선택적 사용이 가능한 애트리뷰트에 대한 내용을 기술하고 선언 순서를 제시하여 코드 품질을 유지한다.</p>
            <p className="nm_txt nm_txt3 pb30">선언하는 속성에는 js에서 DOM 사용과 구분을 위해 쌍따옴표(")를 기본으로 사용한다.</p>



        </article>
      )
    }
}

export default Html;
