import React, { Component } from 'react';


class NamingRule extends Component {
    constructor(props){
      super(props)
    }
    render(){

      return (
        <article>
            <h2>네이밍 규칙</h2>
            <h3>기본 규칙</h3>
            <h4>일반규칙</h4>
            <p className="nm_txt nm_txt3 pb30">이름은 영문 소문자, 숫자, 언더스코어(_)로 작성한다.</p>
            <h4>시작 이름</h4>
            <p className="nm_txt nm_txt3 pb30">이름은 영문 대문자, 숫자로 시작할 수 없다. (잘못된 예 : Btn, 2btn)</p>
            <h4>예약어</h4>
            <ul className="pb30">
              <li>예약어가 있는 경우 예약어를 사용한다.</li>
              <li>예약어는 네이밍 예약어에 근거하여 작성한다.</li>
              <li>예약어가 없으면, 종류와 특성을 나타내도록 네이밍한다.</li>
            </ul>
            <h4>영문 소문자, 숫자, 언더스코어(_) 조합</h4>
            <ul className="pb30">
              <li>영문 소문자, 숫자, 언더스코어만 사용을 권장한다.</li>
              <li>언더스코어는 2개 이상의 단어를 조합할 때만 사용한다.</li>
              <li>단어와 숫자를 조합하는 경우 언더스코어는 생략한다.</li>
              <li>언더스코어가 포함된 예약어는 숫자, 영문 소문자와 조합하여 사용할 수 있다.</li>
              <li>언더스코어를 이용하여 3단계를 초과하여 조합하지 않는다.</li>
            </ul>

            <h4>id, class</h4>
            <ul className="pb30">
              <li>id는 문서 전체의 고유 식별자 이므로 한 문서에서 동일한 id를 여러 번 사용하지 않는다.</li>
              <li>레이아웃을 제외한 id는 스타일을 지정하지 않는다.</li>
              <li>class는 문서에서 여러 번 사용할 수 있다.</li>
            </ul>
            <h4>예약어</h4>
            <div className="cvt_tb">
              <table>
                <thead>
                  <tr>
                    <th>예약어</th>
                    <th>범위</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>#wrap</td>
                    <td>페이지 전체 영역</td>
                  </tr>
                  <tr>
                    <td>#header</td>
                    <td>머리글 영역</td>
                  </tr>
                  <tr>
                    <td>#content</td>
                    <td>콘텐츠 영역</td>
                  </tr>
                  <tr>
                    <td>#footer</td>
                    <td>바닥글 영역</td>
                  </tr>
                  <tr>
                    <td>#pop_wrap</td>
                    <td>팝업 전체 영역</td>
                  </tr>
                  <tr>
                    <td>tb</td>
                    <td>table</td>
                  </tr>
                  <tr>
                    <td>btn</td>
                    <td>button</td>
                  </tr>
                  <tr>
                    <td>i</td>
                    <td>icon</td>
                  </tr>
                  <tr>
                    <td>txt</td>
                    <td>text</td>
                  </tr>
                  <tr>
                    <td>inp</td>
                    <td>input</td>
                  </tr>
                  <tr>
                    <td>sel</td>
                    <td>select</td>
                  </tr>
                  <tr>
                    <td>lst</td>
                    <td>list</td>
                  </tr>
                  <tr>
                    <td>sys</td>
                    <td>system</td>
                  </tr>
                </tbody>
              </table>
            </div>

            <p className="nm_txt nm_txt3 pb10"></p>
            <p className="nm_txt nm_txt3 pb10"></p>
            <p className="nm_txt nm_txt3 pb10"></p>
            <p className="nm_txt nm_txt3 pb10"></p>





        </article>
      )
    }
}

export default NamingRule;
